export default {
  name: 'QPagination',
  model: {
    prop: 'value', // 指向props的参数名
    event: 'change'// 事件名称
  },
  props: {
    value: {
      type: Number,
      default: 0
    },
    length: {
      type: Number,
      default: 0
    },
    fmt: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      page: this.value,
      total: this.length
    }
  },
  watch: {
    page(value) {
      this.$emit('change', value)
    }
  },
  created() {},
  mounted() {},
  methods: {
    input(p) {
      this.$router.push(this.fmt.replace(':p', p))
    }
  }
}
